import React, {Component} from 'react'
import { connect } from 'react-redux'
import Likedby from './Likedby'
import Commentlist from './Commentlist'
//import { Link } from 'react-router-dom'
import {compose} from 'redux'
import { firestoreConnect } from 'react-redux-firebase'

class projectReactions extends Component {

    state={
      commentlist: false,
      likelist: false
    }

    handleLike=(project)=>{
        this.props.updateProject(project.id,project, 'like', [[this.props.auth.uid], [this.props.profile.firstName]] )
        let a= this.props.profile.firstName +' liked your project '+ project.title
        this.props.updateProject(this.props.authorid, a, 'notify',0 )
        console.log(project.Likes, this.props.auth.uid, this.props.profile.firstName)
      }
  
    handleCommentList=()=>{
        this.setState({
          commentlist: (!this.state.commentlist)
        })
      }

    handleLikeList=()=>{
        this.setState({
          likelist: (!this.state.likelist)
        })
      }

    handleUnlike=(project)=>{
        var i=0;var array=[], array2=[];
        for(i; i<project.Likedbyid.length; i++)
        if(project.Likedbyid[i] !==this.props.auth.uid){array.push(project.Likedbyid[i]);array2.push(project.Likedby[i]) }
        console.log(this.props.auth.uid, array)
        this.props.updateProject(project.id,project, 'unlike', [array, array2] )
      }

    render(){
        let Liked=false; var i=0;
        
        let {auth, project, profile,users}= this.props
        console.log(profile, 'profile')
        console.log(this.props, 234567834567834567)
        for(i; i<project.Likedbyid.length; i++){if(project.Likedbyid[i]===auth.uid) Liked=true}
        return(
        <div>
        {Liked===false ?<div class="btn-floating left waves-effect waves-light z-depth-10 li" onClick={()=>this.handleLike(project)}></div>
        : <div class="btn-floating waves-effect left waves-light z-depth-10 li2" onClick={()=>this.handleUnlike(project)}></div>}
               
               
               <div class="row">
               <div class="col m3 card grey lighten-3 z-depth-1" onClick={this.handleLikeList}>
               <m class='black-text'  >Liked by {project.Likes}</m>
               {this.state.likelist===true ? <Likedby list= {project.Likedby}/> : null}
               </div></div>
               
                
               <div className="black-text card z-depth-1 grey lighten-3 comm" >
               <h6 onClick={this.handleCommentList} >Comments</h6><hr/>
               {this.state.commentlist===true ? 
                    <Commentlist list={project.Comments} project={project} profile={profile} users={users} auth={auth} updateProject={this.props.updateProject}/>
                 : null}
               </div>
        
        </div>)
    }
}

const mapStateToProps=(state)=>{
    console.log(state)
    return {
      auth: state.firebase.auth,
      profile: state.firebase.profile
    }
  }


  export default compose(
    firestoreConnect(() => ['project']),
    connect(mapStateToProps)
    )(projectReactions)
