import moment from 'moment'
import React  from 'react'
import { authIsReady } from 'react-redux-firebase';

const notifications=(props) =>{
    const { notifications ,aid} = props;
    let i=1;
    return(
      <div className="card z-depth-2 a">
        <div className="card-content">
          <span className="card-title blue-text">Notifications</span>
          <ul className="online-users">
            { notifications && notifications.map(item =>{
                if(item.idd===aid && i<10)
              return <li key={item.id}>
                {i++})<span>{item.notifify}</span>
                <div className="note-date grey-text">{moment(item.createdAt.toDate()).fromNow()}</div>
                 
              </li>
            })}
          </ul>
        </div>
      </div>
    )
}

export default notifications;