import React, { Component } from 'react'
import CommentDetail from './CommentDetail'
export class Commentlist extends Component {

    state={
        comment:'',
        w:''
    }

    handleChange=(e)=>{
        this.setState({
        [e.target.id]: e.target.value,
        w:e
        })

    }

    deleteComment=(a)=>{
        console.log(a)
    }

    submitComment=(profile, project,auth)=>{
        if(this.state.comment)
        {
        this.props.updateProject(project.id, project, 'comment', {name:profile.firstName, post:this.state.comment, aid:auth.uid} )
        let a= this.props.profile.firstName +' added comment on your project'+ project.title
        this.props.updateProject(this.props.project.AuthorId, a, 'notify',0 )
        this.setState({comment:''})
        let p= this.state.w
        p.target.value=''}
        
    }

    render() {
        let {list, project, profile, auth, updateProject, users}= this.props
        var i=0;
        return (
            <div >
            
            <div className="row">
                 <div className="col s5 m10 input-field">
                <input type="text" id='comment' onChange={this.handleChange}  className="input-area"/>
                <label htmlFor="comment">Type Comment</label>
                { list && list.map(Comment => {
                    return (
                      <div>
                      <CommentDetail project= {project} comment={Comment} users={users} i={i} list={list} auth ={auth} updateProject={updateProject}/>
                      {console.log(i++)}
                      </div>
                      
                    )
                  })}
                 </div>
                  <div className="col s5 m1 offser-m5"> 
                    <button class="btn-floating blue-text waves-effect i22 z-depth-0" type="submit" onClick={()=>this.submitComment(profile, project, auth)}>
                    
                    </button>

                  </div>
                </div>
            </div>
        )
    }
}

export default Commentlist
