import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Allprofiles extends Component {
    

    render() {
        const {users, aid}= this.props 
        return (
            <div className="card mar right offser-m1 right z-depth-4">
            <div className="card-title blue-text">
            You May Know!! </div>
            {
                users && users.map(user=>{
                    if(user.id !==aid)
                    return(<div className="card center">
                    <Link to={'/profile/'+ user.id}>
                    {user&& user.image?
                        <b><br/><img src={user.image} width="80" height="80" class="img"/></b>
                        :<i class="large blue-text material-icons">account_circle</i>}
                    </Link><br/>
                    <Link to={'/profile/'+ user.id} class="black-text"><div class ="m10 pink lighten-3">{user.firstName} {user.lastName}</div>
                    </Link>
                    </div>)
                    else return null
                })
            }
            </div>
            
        )
    }
}

export default Allprofiles
