import React, {Component} from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {compose} from 'redux'
import { firestoreConnect } from 'react-redux-firebase'
import {deleteProject } from '../../store/actions/projectActions'
import {updateProject } from '../../store/actions/projectActions'
import {Redirect} from 'react-router-dom'
import ProjectReactions from './projectReaction'



class projectdetails extends Component {

    state={
      comment: false
    }

    handleClick = () => {
      if (window.confirm('Are you sure you wish to delete this item?'))
      {this.props.deleteProject(this.props.match.params.id)
      this.props.history.push('/')}
    } 
           
      render(){

        var id= (this.props.match.params.id);
        const kk= '/edit/'+id;
        const {auth, users}= this.props
        const a= this.props.project
        let project=false
        let author=false
        if(a)  project= a.find(project => (project.id  === id ));
        if(project && users) author= users.find(author => (author.id  === project.AuthorId ));

        
        if(auth.uid && project)
        if(!auth.uid) return <Redirect to='/SignIn'/>
        if(project)
        return (
            <div class="container section project-details">
            <div class="card z-depth-0">
                <div class="card-content">
                <div class="title blue-text">{project.title}
                  {
                      auth.uid===project.AuthorId ?
                    <Link to= {kk}> 
                    <button class="right btn-floating butt z-depth-10"></button></Link> : null
                  }
                  </div>
                  <div>
                    <div class="left pimg">{author &&author.image ?<img src={author.image} width="55" height="55" class="img"/>
                                                                : <i class="medium blue-text material-icons">account_circle</i>}
                    </div>

                    <div class="left adi">
                    {author?
                    <b>Posted by <o>{author.firstName} {author.lastName} </o><br/></b>:null}
                    <c class="grey-text-small">{project.createdAt.toDate().toString().slice(0,15)}</c>
                    <br/>
                    <br/>
                    {project.image? <img id="k" src={project.image} class="left"/>:null}
                    </div>
                    <div class="row">
                    <p >{project.content}</p>   
                    </div>
                    <div class="rkn"><hr/>
                <ProjectReactions auth= {auth} project={project} users={users} updateProject={this.props.updateProject} authorid={project.AuthorId}/>
               </div>
                </div>
                </div>
                
            </div>


        
            <div className='center'>
            {auth.uid===project.AuthorId ? 
              <button className="btn white black-text z-depth-10" onClick={this.handleClick}>Delete Post</button>
               : null}
              </div>
        </div>
        )
      else return null}
    }


    const mapStateToProps = (state) => {
        return {
          project:state.firestore.ordered.project,
          users:state.firestore.ordered.users,
          auth:state.firebase.auth
        }
      }

    const mapDispatchToProps =(dispatch)=>{
      return{
        deleteProject:(id)=>{dispatch(deleteProject(id))},
        updateProject:(id,project, job,authid)=>{dispatch(updateProject(id,project, job, authid))}
      }
    }
     
    export default compose(
      firestoreConnect(() => ['project', 'users']),
      connect(mapStateToProps, mapDispatchToProps)
      )(projectdetails)
