import React , {Component} from 'react'
import Notification from './notification'
import Projectlist from '../projects/projectlist'
import { connect } from 'react-redux'
import {compose} from 'redux'
import { firestoreConnect } from 'react-redux-firebase'
import {Redirect} from 'react-router-dom'
import notifications from './notification'

class dashboard extends Component{

    render(){
        const {projects, auth, notifications, users}= this.props
        console.log(users, 345678)
        if(!auth.uid) return <Redirect to='/SignIn'/>

        return(
            <div className="dashboard">
                <div className="row">
                 <div className="col l6">
                 <Projectlist projects= {projects} users={users} />
                 </div>
                  <div className="col s12 m5 offser-m1"> 
                  <br/>
                  <Notification class="a" notifications={notifications} aid={auth.uid}/>
                  </div>
                </div>
                </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
      projects: state.firestore.ordered.project,
      users: state.firestore.ordered.users,
      notifications : state.firestore.ordered.notifications,
      auth:state.firebase.auth
    }
  }
  
  export default compose(
    firestoreConnect(() => ['project', 'notifications', 'users']),
    connect(mapStateToProps)
    )(dashboard)
