import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import {createProject } from '../../store/actions/projectActions'
import ImageUpload, {k} from '../ImageUpload';



class createproject extends Component {
  
  state = {
    title: '',
    content: '',
    image:'',
    Likes: 0,
    AuthorId:this.props.auth.uid,
    AuthorFirstName: this.props.profile.firstName,
    AuthorImage: this.props.profile.image,
    AuthorLastName: this.props.profile.lastName
  }
 
  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,     
    })
  }

  
  handleDone = (k) => {
   if(this.state.title)
   {
   if(k)
    this.setState({
      image: k
    })
    document.getElementById("element1").style.display = "none";
    document.getElementById("element2").style.display = "inline";
  }
    
  }

  handlePost=()=>{
    if(this.state.title)
    { 
      console.log(this.state) 
      this.props.createProject(this.state);
      this.props.history.push('/')
    }
    else 
    {
      document.getElementById("element2").style.display = "none";
      document.getElementById("element1").style.display = "inline";
    }
    
  }

  handleCancel=()=>{
    this.props.history.push('/')
  }
  
  render() {
    const {auth}= this.props;
    if(!auth.uid)return  <Redirect to='/SignIn'/>
    return (
      <div className="container"><br/><br/>
        <div className="white card" >
        <div class="card-content">
          <h5 className="grey-text text-darken-3">Create New Project
           <button class="btn-floating white right">
           <i class="material-icons black-text" onClick={this.handleCancel}>cancel</i>
           </button>
           </h5>
          <div className="input-field">
            <label htmlFor="title">Title</label>
            <input type="text" id='title' onChange={this.handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="content">Project Content</label>
            <textarea id="content" class="materialize-textarea" onChange={this.handleChange} ></textarea>
          </div>
          <div className="input-field grey-text">Add Images
          <ImageUpload/>
          </div>
          <div className="input-field">
          
           <button className="btn z-depth-2" onClick={()=>this.handleDone(k)} id="element1">1.Done</button>
           --------➤
           <button className="btn z-depth-2 a" onClick={this.handlePost} id="element2">2.Post Project</button>
           
          </div>
        </div>
      </div>
      </div>
    )
  }
}


const mapDiaptchToProps=(dispatch)=>{
  return{
    createProject:(project)=>{dispatch(createProject(project))}
  }

}

const mapStateToProps=(state)=>{
  return {
    auth: state.firebase.auth,
    profile: state.firebase.profile
  }
}


export default connect(mapStateToProps, mapDiaptchToProps)(createproject)