import React from 'react'
import { Link } from 'react-router-dom'
import {connect } from 'react-redux'
import {signOut} from '../../store/actions/projectActions' 



const signedinlinks = (props) => {
  const handleClick=(props)=>{
    if (window.confirm('Are you sure you wish to Log Out?'))
    props.signOut()
  }

  if(props.details.isEmpty !== true)
  return (
    <div class="blue">
    <ul class="right">
    <li><div class=" pink"><Link to= '/Create'> Create Projects</Link></div></li>
    <li><Link to= {props.k}> 
    {props.profile.image ? <img src={props.profile.image} width="50" height="50" class="img"/>:<i class="material-icons right">account_circle</i>}
    </Link>
    </li>
    <li><Link onClick={()=>handleClick(props)}> Log Out </Link></li>
    <li><Link to= '/upload'> UpDate DP</Link></li>
    </ul>
    <Link to='/' class="left"><i class="material-icons">home</i></Link>
    </div>
  )
  else return null
}

const mapDispatchToProps=(dispatch)=>{
  return{
    signOut: ()=> dispatch(signOut())
  }
}

export default connect(null, mapDispatchToProps)(signedinlinks);